import {Map, List, fromJS} from 'immutable';

export default (state = Map(), action) => {

  if (action.type === 'RESOURCE_LIST') {
    return state.set('resourcelist', List(action.resourcelist));
  }

  if (action.type === 'RESOURCE_ADD') {
    return state.update('resourcelist', value => {
      return value.reverse().concat(action.info).reverse()
    });
  }

  if (action.type === 'RESOURCE_EDIT') {
    return state.update('resourcelist', value => {
      return value.update(action.index, value => {
        return action.info
      })
    });
  }

  if (action.type === 'RESOURCE_DELETE') {
    return state.update('resourcelist', value => {
      return value.delete(action.index)
    });
  }

  return state;
};



