import callApi from '../../utils/callApi';

/**
 * ??????????????????
 * @returns {function()}
 */
const getList = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/page/list';
    return callApi.get(requestUrl, formdata).then(res => {
      return Promise.resolve(dispatch({
        type: 'page_list',
        pagetype: formdata.type,
        pagination: res.data.pagination,
        pagelist: res.data.results
      }))
    });
  }
}

/**
 * ??????????????????
 * @returns {function()}
 */
const getDetail = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/page/detail';
    if (formdata._id) {
      return callApi.get(requestUrl, formdata).then(res => {
        return Promise.resolve(dispatch({
          type: 'page_detail',
          detail: res.data
        }))
      });
    }
    return Promise.resolve(dispatch({
      type: 'page_detail',
      detail: {}
    }))
    
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postAddPage = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/page/add';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'page_add',
        info: res.data
      }));
    });
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postEditPage = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/page/update';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'page_edit',
        index: formdata.index,
        info: res.data
      }));
    });
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postChangePageStatus = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/page/changeStatus';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'page_delete',
        index: formdata.index
      }));
    });

  }
}

/**
 * ???????????????
 * @param formdata
 * @returns {function()}
 */
const postCleanPage = () => {
  return (dispatch) => {
    const requestUrl = '/api/page/clean';
    return callApi.post(requestUrl).then(res => {
      return Promise.resolve(dispatch({
        type: 'page_clean'
      }));
    });
  }
}

export default {
  getList,
  getDetail,
  postAddPage,
  postEditPage,
  postCleanPage,
  postChangePageStatus
}



