import callApi from '../../utils/callApi';

/**
 * ??????????????????
 * @returns {function()}
 */
const getList = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/post/list';
    return callApi.get(requestUrl, formdata).then(res => {
      return Promise.resolve(dispatch({
        type: 'post_list',
        posttype: formdata.type,
        pagination: res.data.pagination,
        postlist: res.data.results
      }))
    });
  }
}

/**
 * ??????????????????
 * @returns {function()}
 */
const getDetail = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/post/detail';
    if (formdata._id) {
      return callApi.get(requestUrl, formdata).then(res => {
        return Promise.resolve(dispatch({
          type: 'post_detail',
          detail: res.data
        }))
      });
    }
    return Promise.resolve(dispatch({
      type: 'post_detail',
      detail: {}
    }))
    
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postAddPost = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/post/add';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'post_add',
        info: res.data
      }));
    });
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postEditPost = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/post/update';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'post_edit',
        index: formdata.index,
        info: res.data
      }));
    });
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postChangePostStatus = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/post/changeStatus';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'post_delete',
        index: formdata.index
      }));
    });

  }
}

/**
 * ???????????????
 * @param formdata
 * @returns {function()}
 */
const postCleanPost = () => {
  return (dispatch) => {
    const requestUrl = '/api/post/clean';
    return callApi.post(requestUrl).then(res => {
      return Promise.resolve(dispatch({
        type: 'post_clean'
      }));
    });
  }
}

export default {
  getList,
  getDetail,
  postAddPost,
  postEditPost,
  postCleanPost,
  postChangePostStatus
}



