import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import { Link } from 'react-router'
import '../styles.less'
import { Table, Menu, Popconfirm, message, Tag, Button, Modal, Icon } from 'antd';


import postActions from '../action'
import appActions from '../../app/action'

import {dateToString} from '../../../utils/index';

export class Post extends Component {

  static propTypes = {
    post: PropTypes.object,
    router: PropTypes.object,
    appActions: PropTypes.object,
    postActions: PropTypes.object,
    children: PropTypes.object
  }

  componentDidMount () {
    const type = this.props.post.get('posttype');
    this.getPostList({
      page: 1,
      type
    });
  }

  getCloums () {

    const type = this.props.post.get('posttype') || 'post';
    /**
     * table??????
     * @type {*[]}
     */
    return [{
      title: '????????????',
      dataIndex: 'post_title',
      key: 'title',
      render: (text, record) => {
        return (
          <a href={`/post/${record.post_slug}.html`} target="_blank">{text}</a>
        )
      }
    }, {
      title: '?????????',
      dataIndex: 'post_author',
      key: 'author',
      render: (doc) => {
        if (doc) {
          return (
            <span>{doc.user_realname}</span>
          )
        }
          return (
            <span>?????????</span>
          )
        
      }
    }, {
      title: '????????????',
      dataIndex: 'post_term',
      key: 'term',
      render: (doc) => {
        return (
          <Tag color="#87d068">{doc.term_name}</Tag>
        )
      }
    }, {
      title: '????????????',
      width: 90,
      dataIndex: 'post_status',
      key: 'status',
      render: (text) => {
        const statusObj = {
          '-1': {
            color: '#f50',
            content: '?????????'
          },
          1: {
            color: '#87d068',
            content: '?????????'
          },
          0: {
            color: '#108ee9',
            content: '??????'
          }
        };

        return (
          <span style={{color: statusObj[text].color}}>{statusObj[text].content}</span>
        );
      }
    }, {
      title: '????????????',
      dataIndex: 'post_date',
      key: 'date',
      render: (text) => {
        return dateToString(text);
      }
    }, {
      title: '????????????',
      dataIndex: 'post_modified',
      key: 'modify',
      render: (text) => {
        return dateToString(text);
      }
    }, {
      title: '??????',
      key: 'handle',
      render: (text, record, index) => {
        const self = this, { _id } = record;
        const formdata = {
          _id
        }
        function handlePostRecover() {
          formdata.post_status = '1';
          self.handleChangeStatus(formdata, index)
        }
        function handlePostTrash() {
          formdata.post_status = '-1';
          self.handleChangeStatus(formdata, index)
        }
        function handlePostDelete() {
          formdata.post_status = '-999';
          self.handleChangeStatus(formdata, index)
        }

        return (
          <span>
            <a href={`/admin/post/${record._id}.html`} target="_blank">??????</a>
            <span className="ant-divider" />
            {
              type === 'post' && <Link to={`/post/edit/${record._id}`}>??????</Link>
            }
            {
              type === 'post' && <span className="ant-divider" />
            }
            {
              type === 'trash' ?
                <Popconfirm title="?????????????????????????" onConfirm={handlePostRecover} >
                  <a href="#">??????</a>
                </Popconfirm> :
                <Popconfirm title="?????????????????????????" onConfirm={handlePostTrash} >
                  <a href="#">??????</a>
                </Popconfirm>
            }
            {
              type === 'trash' && <span className="ant-divider" />
            }
            {
              type === 'trash' && <Popconfirm title="?????????????????????,?????????????" onConfirm={handlePostDelete} >
                <a href="#">????????????</a>
              </Popconfirm>
            }

          </span>
        )
      },
    }];
  }

  /**
   * ??????????????????
   */
  getPostList (params) {
    const { appActions, postActions } =  this.props;

    appActions.loading(true).then(() => {
      return postActions.getList(params)
    }).then(() => {
      appActions.loading(false);
    }).catch(err => {
      message.error(err.msg);
    })
  }

  /**
   * ??????????????????
   * @param e
   */
  handleMenuClick = (e) => {
    this.getPostList({
      page: 1,
      type: e.key
    });
  }


  /**
   * ????????????
   * @param page
   */
  handleTableChange = (pagination) => {

    const type = this.props.post.get('posttype');
    this.getPostList({
      page: pagination.current,
      type
    });
  }

  /**
   * ??????????????????
   * @param _id
   */
  handleChangeStatus = (formdata, index) => {
    this.props.postActions.postChangePostStatus({
      index,
      _id: formdata._id,
      post_status: formdata.post_status
    }).then(data => {
      message.success('????????????');
    }).catch(err => {
      message.error(err.msg);
    })
  }

  /**
   * ??????????????????????????????
   */
  handleTrashModal = () => {
    Modal.confirm({
      title: '???????????????',
      content: '?????????????????????,????????????????????????!',
      okText: '????????????',
      onOk: () => {
        return this.props.postActions.postCleanPost().then(data => {
          message.success('????????????');
        }).catch(err => {
          message.error(err.msg);
        })
      }
    });
  }

  /**
   * ??????????????????
   */
  handleAddPost = () => {
    this.props.router.push({
      pathname: '/post/new'
    })
  }

  render() {
    const {post} = this.props;
    const type = post.get('posttype') || 'post';
    const list = post.get('postlist') && post.get('postlist').toArray();
    const pagination = post.get('pagination') && post.get('pagination').toJS();
    if (this.props.children) {
      return (
        <div>
          {this.props.children}
        </div>
      )
    }
      const Columns = this.getCloums();
      return (
        <div>
          {
            type === 'trash' && <Button style={{float: 'right', top: '10px'}} type="danger" onClick={this.handleTrashModal}><Icon type="delete" />???????????????</Button>
          }
          {
            type === 'post' && <Button style={{float: 'right', top: '10px'}} type="primary" onClick={this.handleAddPost}><Icon type="file-add" />????????????</Button>
          }
          <Menu onClick={this.handleMenuClick}
                selectedKeys={[type]}
                mode="horizontal">
            <Menu.Item key="post">
              ????????????
            </Menu.Item>
            <Menu.Item key="trash" style={{marginLeft: 20}}>
              ???????????????
            </Menu.Item>
          </Menu>

          <Table
            bordered
            rowKey="_id"
            columns={Columns}
            dataSource={list}
            className="mt20"
            pagination={pagination}
            onChange={this.handleTableChange}
          />
        </div>
      )
    
  }

}

export default connect((state) => {
  return  {
    post: state.get('post')
  }
}, dispatch => {
  return {
    postActions: bindActionCreators(postActions, dispatch),
    appActions: bindActionCreators(appActions, dispatch)
  }
})(Post);



