import callApi from '../../utils/callApi';

/**
 * ??????????????????
 * @returns {function()}
 */
const getList = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/resource/list';
    return callApi.get(requestUrl, formdata).then(res => {
      return Promise.resolve(dispatch({
        type: 'RESOURCE_LIST',
        resourcelist: res.data.results
      }))
    });
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postAddResource = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/resource/add';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'RESOURCE_ADD',
        info: res.data
      }));
    })
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postCreateResource = (formdata) => {
    return (dispatch) => {
        const requestUrl = '/api/resource/create';
        return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
            return Promise.resolve(dispatch({
                type: 'RESOURCE_ADD',
                info: res.data
            }));
        })
    }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postEditResource = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/resource/update';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'RESOURCE_EDIT',
        index: formdata.index,
        info: res.data
      }));
    });
  }
}

/**
 * ????????????
 * @param formdata
 * @returns {function()}
 */
const postDeleteResource = (formdata) => {
  return (dispatch) => {
    const requestUrl = '/api/resource/delete';
    return callApi.post(requestUrl, JSON.stringify(formdata)).then(res => {
      return Promise.resolve(dispatch({
        type: 'RESOURCE_DELETE',
        index: formdata.index
      }));
    });
  }
}

export default {
  getList,
  postAddResource,
  postCreateResource,
  postEditResource,
  postDeleteResource
}



