import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import { Table, Menu, Popconfirm, message, Button, Icon } from 'antd';


import resourceActions from '../action'
import appActions from '../../app/action'
import ResourceForm from './ResourceForm';

export class Resource extends Component {

  static propTypes = {
    resource: PropTypes.object,
    appActions: PropTypes.object,
    resourceActions: PropTypes.object
  }

  constructor (props) {
    super(props);

    /**
     * table??????
     * @type {*[]}
     */
    this.columns = [{
      title: '????????????',
      width: 150,
      dataIndex: 'resource_name',
      key: 'name',
    }, {
      title: '????????????',
      width: 100,
      dataIndex: 'resource_type',
      key: 'type',
    }, {
      title: '????????????',
      dataIndex: 'resource_url',
      key: 'url',
    }, {
        title: '????????????',
        dataIndex: 'resource_url',
        render: (text) => {
          return (
            <span>
              <img src={text} width="200" height="100" />
            </span>
          )
        }
    }, {
      title: '??????',
      render: (text, record, index) => {

        const self = this;
        function handleResourceDelete() {
          self.handleResourceDelete(record, index)
        }

        return (
          <span>
            <Popconfirm title="?????????????????????????" onConfirm={handleResourceDelete} >
              <a href="#">??????</a>
            </Popconfirm>
          </span>
        )
      },
    }];
  }

  state = {
    index: null,
    formType: 'add',
    formValues: null,
    formVisible: false
  }

  componentDidMount () {
    this.getResourceList();
  }

  /**
   * ????????????
   * @param page
   */
  handleTableChange = (pagination) => {
    this.getResourceList({
      page: pagination.current
    });
  }

  /**
   * ??????????????????
   */
  getResourceList = (params) => {
    const { appActions, resourceActions } =  this.props;
    appActions.loading(true).then(() => {
      return resourceActions.getList(params)
    }).then(() => {
      appActions.loading(false);
    }).catch(err => {
      message.error(err.msg);
    })
  }

  /**
   * ????????????
   * @param e
   */
  handleResourceAdd = (e) => {
    this.setState({
      formType: 'add',
      formValues: null,
      formVisible: true
    })
  }

  /**
   * ??????modal?????????
   */
  handleHideModal = () => {
    this.setState({
      formValues: null,
      formVisible: false
    })
  }

  /**
   * ????????????
   * @param formdata
   */
  handleResourceSubmit = (formdata) => {
    const {resourceActions} = this.props;
    resourceActions.postCreateResource(formdata).then(data => {
        this.handleHideModal();
    }).catch(err => {
        if (typeof err.msg === 'object') {
            message.error(err.msg.message)
        } else {
            message.error(err.msg)
        }
    });
  }

  handleResourceEdit = (formValues, index) => {
    this.setState({
      index,
      formType: 'edit',
      formVisible: true,
      formValues
    })
  }


  /**
   * ??????????????????
   * @param _id
   */
  handleResourceDelete = (formdata, index) => {
    this.props.resourceActions.postDeleteResource({
      index,
      _id: formdata._id,
    }).then(data => {
      message.success('????????????');
    }).catch(err => {
      message.error(err.msg);
    })
  }

  render() {

    const {resource} = this.props;
    const list = resource.get('resourcelist') && resource.get('resourcelist').toArray();
    const pagination = resource.get('pagination') && resource.get('pagination').toJS();

    return (
      <div>

        <Button style={{float: 'right', top: '10px'}} type="primary" onClick={this.handleResourceAdd}><Icon type="folder-add" />????????????</Button>
        <Menu selectedKeys={['list']}
              mode="horizontal">
          <Menu.Item key="list">
            ????????????
          </Menu.Item>
        </Menu>

        <Table
          bordered
          rowKey="_id"
          columns={this.columns}
          dataSource={list}
          className="mt20"
          pagination={pagination}
          onChange={this.handleTableChange}
        />
        {
          this.state.formVisible && <ResourceForm
            formType={this.state.formType}
            formValues={this.state.formValues}
            handleHide={this.handleHideModal}
            handleOK={this.handleResourceSubmit}
          />
        }
      </div>
    )
  }

}

export default connect((state) => {
  return  {
    resource: state.get('resource')
  }
}, dispatch => {
  return {
    resourceActions: bindActionCreators(resourceActions, dispatch),
    appActions: bindActionCreators(appActions, dispatch)
  }
})(Resource);



