import {Map, List, fromJS} from 'immutable';

export default (state = Map(), action) => {

  if (action.type === 'page_list') {
    return state.merge({
      pagetype: action.pagetype,
      pagination: fromJS(action.pagination),
      pagelist: List(action.pagelist)
    });
  }

  if (action.type === 'page_delete') {
    return state.update('pagelist', value => {
      return value.delete(action.index)
    });
  }

  if (action.type === 'page_detail') {
    return state.set('pagedetail', action.detail);
  }

  if (action.type === 'page_clean') {
    return state.update('pagelist', value => {
      return value.clear()
    });
  }

  return state;
};



