import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import { Link } from 'react-router';

import 'antd/dist/antd.css';  // or 'antd/dist/antd.less'
import './styles.less';

import { Layout, Menu, Breadcrumb, Icon, Avatar } from 'antd';
const { Header, Content, Footer, Sider } = Layout;
const SubMenu = Menu.SubMenu;

function itemRender(route, params, routes, paths) {
  const last = routes.indexOf(route) === routes.length - 1;
  return last ?
    <span><Icon type={route.icon} style={{marginRight: '3px'}} />{route.breadcrumbName}</span> :
    <Link to={paths.join('/')}><Icon type={route.icon} style={{marginRight: '3px'}} />{route.breadcrumbName}</Link>;
}

export class App extends Component {

  static propTypes = {
    children: PropTypes.node,
    routes: PropTypes.array,
    location: PropTypes.object,
    router: PropTypes.object
  };

  state = {
  }

  goHome = () => {
    this.props.router.push({ pathname: '/' });
  }

  /**
   * menu click handle
   * @param item
   * @param key
   * @param keyPath
   */
  menuHandleClick = (item) => {
    const pathname = this.props.location.pathname;
    if (item && item.key) {
      if (pathname !== item.key) {
        this.props.router.push({
          pathname: item.key
        })
      }
    }
  }

  render() {
    const {routes, location} = this.props;
    let path = location.pathname;
    if (path.split('/')[2] === 'edit') {
      path = '/post';
    }
    const {userinfo} = window.INIT_DATA;
    const avatar = userinfo.avatar || '';

    return (
      <Layout>
        <Sider width={180}>
          <div className="logo" onClick={this.goHome} />
          <Menu theme="dark"
                selectedKeys={[path]}
                mode="inline"
                defaultOpenKeys={['article', 'page', 'resource', 'system']}
                onClick={this.menuHandleClick} >

            <SubMenu key="article" title={<span><Icon type="folder" /><span>????????????</span></span>}>
              <Menu.Item key="/post">????????????</Menu.Item>
              <Menu.Item key="/post/new">????????????</Menu.Item>
            </SubMenu>

            <SubMenu key="page" title={<span><Icon type="folder" /><span>????????????</span></span>}>
              <Menu.Item key="/page">????????????</Menu.Item>
              <Menu.Item key="/page/new">????????????</Menu.Item>
            </SubMenu>

            <SubMenu key="resource" title={<span><Icon type="folder" /><span>????????????</span></span>}>
              <Menu.Item key="/resource">????????????</Menu.Item>
            </SubMenu>

            <SubMenu key="system" title={<span><Icon type="setting" /><span>????????????</span></span>}>
              <Menu.Item key="/system/posts">????????????</Menu.Item>
              <Menu.Item key="/system/term">????????????</Menu.Item>
              <Menu.Item key="/system/user">????????????</Menu.Item>
            </SubMenu>

          </Menu>
        </Sider>
        <Layout style={{ overflow: 'auto'}}>
          <Header style={{ background: '#fff', padding: 0 }} >

            <Menu mode="horizontal" className="ant-avatar-wrap" >
              <SubMenu title={
                <span className="ant-avatar-wraper">
                  {
                    avatar && <Avatar src={avatar} className="head-avatar" />
                  }
                  {
                    !avatar && <Avatar icon="user" className="head-avatar" />
                  }
                  {userinfo.user_realname}
                </span>
              }>
                <Menu.Item key="logout">
                  <a href="/admin/logout"><Icon type="logout" />??????</a>
                </Menu.Item>
              </SubMenu>
            </Menu>

          </Header>
          <Content style={{ margin: '0 16px' }}>
            <Breadcrumb style={{ margin: '20px 0' }} routes={routes} itemRender={itemRender} />
            <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
              {this.props.children}
            </div>
          </Content>
          <Footer style={{ textAlign: 'center' }}>
            ??2017 Powered by ??????????????????
          </Footer>
        </Layout>
      </Layout>
    );
  }
}

function mapStateToProps(state) {
  return {
    toast: state.get('toast'),
    routing: state.get('routing')
  };
}

export default connect(mapStateToProps)(App);



