import 'isomorphic-fetch';
import 'fetch-ie8';
import {context} from '../../Config';
// ?????? fetch ??????????????? ??? https://github.com/github/fetch
const defaultOptions = {
  credentials: 'include', //???????????????????????? cookie ??????????????????
  headers: {
    Accept: 'application/json',
    'Content-Type': 'application/json; charset=utf-8'
  }
};

// ????????????????????????
function checkStatus(response) {
  const {status} = response;
  if (status >= 200 && status < 300) {
    return response;
  }
  const error = new Error(response.statusText);
  error.errorCode = status;
  throw error;
}

/**
 * ?????? fetch
 * ?????????????????????????????????????????????????????????????????????
 * @param url
 * @param body //?????????????????? json ??????
 * @param method // ?????? type  get post delete header put
 * @param options // ???????????????
 * @returns {Promise.<TResult>}
 */
function callApiFn(url, body = {}, method = 'get', options = {}) {
  if (!url) {
    const error = new Error('????????? url');
    error.errorCode = 0;
    return Promise.reject(error);
  }

  let fullUrl = url;
  if (context) {
    fullUrl = `${context}/${url}`;
  }

  const _options = {method, ...defaultOptions, ...options};

  if (method !== 'get' && method !== 'head') {
    //????????? null ??????????????????
    Object.keys(body).forEach((item) => {
      if (body[item] === null || body[item] === '') {
        delete body[item];
      }
    });
    _options.body = body;
  }

  if (method === 'get') {
    const params = [];
    //????????? null ??????????????????
    Object.keys(body).forEach((item) => {
      if (body[item] === null || body[item] === '') {
        delete body[item];
      }
      params.push(`${item}=${body[item]}`);
    });
    fullUrl = `${fullUrl}?${params.join('&')}`;
  }

  return fetch(fullUrl, _options)
    .then(checkStatus)
    .then(response =>
      response.json().then(json => ({json, response}))
    ).then(({json, response}) => {
      if (!response.ok || json.code !== 1) {
        // ???????????????????????????????????????????????????
        //const error = new Error(json.message || '??????????????????');
        //error.errorCode = json.errorCode;
        return Promise.reject(json);
      }
      return json;
    }).catch((error) => {
      return Promise.reject(error);
    });
}

function get(url = '', body = {}) {
  return callApiFn(url, body, 'get')
}

function post(url = '', body = {}) {
  return callApiFn(url, body, 'post')
}

const callApi = {
  get,
  post
}

export default callApi;



