import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {  Modal, Form, Input, Icon, Button, Select, Upload, message } from 'antd';
const Dragger = Upload.Dragger;

export class TermForm extends Component {

  static UploadData = [];
  static propTypes = {
    formValues: PropTypes.object,
    formType: PropTypes.string,
    form: PropTypes.object,
    handleHide: PropTypes.func,
    handleOK: PropTypes.func
  }

  /**
   * ????????????????????????
   */
  handleFormCancel = () => {
    this.props.handleHide();
  }

  /**
   * ????????????????????????
   */
  handleFormOk = () => {
      console.log(this.UploadData)
    this.props.handleOK({
        source: this.UploadData
    });
  }

  render() {
    const title = '????????????';
    const visible = true;
    const self = this;
    const UploadProps = {
      name: 'uploadFiles',
      accept: 'image/gif, image/jpeg, image/png',
      multiple: true,
      action: '/api/resource/upload',
      onChange(info) {
          if (info.file.status !== 'uploading') {
              console.log(info.file, info.fileList);
          }
          if (info.file.status === 'done') {
              const UploadData = [];
              const list = info.fileList;
              list.forEach(item => {
                  const {originName, url} = item.response.data;
                  UploadData.push({
                      resource_name: originName,
                      resource_url: url
                  });
              })
              self.UploadData = UploadData;
              message.success(`${info.file.name} file uploaded successfully`);
          } else if (info.file.status === 'error') {
              message.error(`${info.file.name} file upload failed.`);
          }
      },
    };

    return (
      <div>
        <Modal title={title}
               visible={visible}
               onOk={this.handleFormOk}
               onCancel={this.handleFormCancel}>
          <Dragger {...UploadProps}>
            <p className="ant-upload-drag-icon">
              <Icon type="inbox" />
            </p>
            <p className="ant-upload-text">Click or drag file to this area to upload</p>
            <p className="ant-upload-hint">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p>
          </Dragger>

          <div>
          </div>

        </Modal>
      </div>
    )
  }

}

export default TermForm;



